@import "https://fonts.googleapis.com/css2?family=Lekton:ital,wght@0,400;0,700;1,400&display=swap";
.layout {
  z-index: 1;
  text-align: center;
  background-color: #000c;
  border-radius: 10px;
  padding: 20px;
  position: relative;
  inset: 50px;
}

p {
  font-family: Lekton, monospace;
  font-style: normal;
  font-weight: 400;
  display: inline-block;
  position: relative;
}

.title {
  font-family: Lekton, monospace;
  font-style: normal;
  font-weight: 700;
  display: inline-block;
  position: relative;
}

.title:before {
  content: attr(data-first-letters);
  text-align: center;
  color: #0000;
  z-index: 1;
  background: linear-gradient(135deg, #f6b84666 0%, #f6b84603 100%);
  border-radius: 50%;
  width: 2.2ch;
  height: 1.15em;
  line-height: 1em;
  position: absolute;
  top: 0;
  left: 0;
}

.title:first-letter {
  z-index: 1;
  position: relative;
}

.title:after {
  content: "";
  background: -moz-radial-gradient(left, ellipse cover, #ddd 0%, #fff0 70%);
  background: -webkit-radial-gradient(left, ellipse cover, #ddd 0%, #fff0 70%);
  background: radial-gradient(at 0, #ddd 0%, #fff0 70%);
  height: 1px;
  position: absolute;
  bottom: 0;
  left: -30px;
  right: 0;
}

.main-content {
  left: 0;
}

.btn-link, a {
  text-decoration: none;
}

.content {
  min-width: 200px;
}

@media screen and (width <= 1024px) {
  .layout {
    inset: 70px 0 0;
  }
}

@media screen and (width >= 768px) {
  .content {
    min-width: 600px;
    min-height: 500px;
  }
}

@media screen and (width >= 1024px) {
  .content {
    min-width: 500px;
    min-height: 640px;
  }
}

@media screen and (width >= 1200px) {
  .content {
    min-width: 600px;
    min-height: 640px;
  }
}

@media screen and (width >= 1400px) {
  .content {
    min-width: 700px;
  }
}

.vertical-container {
  background-color: #000;
  width: 100%;
  height: 100vh;
  position: fixed;
  overflow: hidden;
}

.line {
  flex-direction: column;
  justify-content: space-between;
  width: 2px;
  animation: linear infinite move;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
}

.piece {
  background-color: #eca421;
  width: 100%;
  margin-bottom: 20px;
  animation: linear infinite move;
}

@keyframes move {
  0% {
    transform: translateY(100vh);
  }

  100% {
    transform: translateY(-100vh);
  }
}

.ant-menu-item-selected {
  color: #f90 !important;
  background-color: #0000 !important;
}

.circle {
  pointer-events: none;
  z-index: 100;
  background-color: #f7b4389c;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  transition: left .1s, top .1s;
  position: absolute;
  transform: translate(-50%, -50%);
}
